import React from 'react';
import {Tabs} from 'antd';
import GeRen from './GeRen';
import JieJiaRi from './JieJiaRi';
// import {postAction} from 'src/axios';

const TabPane = Tabs.TabPane;

export default class Index extends React.Component{

	state = {
        tabs:[]
    }
    
    componentDidMount() {
        this.setState({
            tabs: [
                {name: "", id: 311},
                {name: "", id: 312},
            ]
        })

        // const userinfo=JSON.parse(getLocal('userInfo'));
        // let uid=userinfo.id;
        // let data={
        //     sourceCode:"pc",
        //     menuId:11,
        //     userId:uid            
        // }
        // postAction('/bigdata/system/menu/labelList',data,0,1).then(res=>{
        //    if(res.success){
        //        this.setState({
        //           tabs:res.obj
        //        })
                
        //    }
        // })
    }
    
    getTabs = id => {
        switch(id){
            case 311:
            return <GeRen/>
            case 312:
            return <JieJiaRi/>
            default:
            return null
        }
    }
    
	render () {
        const {tabs}=this.state;
		return (
            <Tabs defaulActiveKey={311}>
            {
                tabs.map(item=>(
                    <TabPane tab={item.name} key={item.id}>
                        {this.getTabs(item.id)}
                    </TabPane>
                ))
            }                    
            </Tabs>
		)
	}
}
