import React, {Component} from 'react';
import {Select, Input, Button} from 'antd';
// import {postAction} from "src/axios";
const Option = Select.Option;

export default class Filter extends Component {

    constructor(props) {
        super(props);
        this.state = {
            // 
            curCollege:'',
            collegeLists:[],
            // 
            curMajor:'',
            majorLists:[],
            // 
            curGender:'',
            genderLists:[
                {name:"", value: ""},
	            {name: '',value: "0"},
	            {name: '',value: "1"}
            ],

            studentNo:'',
        };
    }

    componentDidMount() {
		this.getColledgeLists();
		this.getMajorLists();
    	this.sureBtn()
    }
    
    // 
    sureBtn = () => {
    	let filterValue={
    		curCollege: this.state.curCollege,
    		curMajor:this.state.curMajor,
            curGender: this.state.curGender,
            studentNo: this.state.studentNo
    	}
    	
    	this.props.getFilterValue(filterValue);
    }
    
    // list
    getMajorLists () {
        this.setState({
            majorLists: [
                {name: '', value: '1'},
                {name: '', value: '2'},
            ]
        })
    	// postAction(
    	// 	"/bigdata/school/listMajorByCollege",
    	// 	{
    	// 		collegeCode: this.state.curCollege
    	// 	}
    	// ).then(
    	// 	(res) => {
    	// 		if (res.success) {
    	// 			this.setState({
    	// 				majorLists: res.obj,
    	// 			})
    	// 		}
    	// 	}
    	// )
    }
    
     // list
    getColledgeLists(){
        this.setState({
            collegeLists: [
                {name: '', value: '1'},
                {name: '', value: '2'},
            ]
        })
    	// postAction(
    	// 	"/bigdata/school/listCollege",
    	// ).then(
    	// 	(res)=>{
    	// 		if (res.success) {
    	// 			this.setState({
    	// 				collegeLists: res.obj,
    	// 			})
    	// 		}
    	// 	}
    	// )
    }
    
    changeFilter(num, value){
    	if (num === 0){ // 
    		this.setState({
                curCollege: value,
            },() =>{
                // this.getMajorLists()
                // this.getGradeLists()
                // this.getClassLists()
            })
    	} else if (num === 1){ // 
    		this.setState({
                curMajor: value,
            },
            () => {
                // this.getGradeLists()
                // this.getClassLists()
            })
    	} else if (num === 2){ // 
    		this.setState({
    			curGender: value,
    		})
    	}
    }

    changeStudentNo(e){
        this.setState({studentNo: e.target.value});
    }

    render () {

        return (
            <div style={{marginBottom: 20, lineHeight: '40px', background: 'rgba(255,255,255,0.6)'}}>
                <span></span>

                <Select 
                	value={this.state.curCollege}
                	placeholder="" 
                	style={{ width: 180, marginRight: 10 }} 
                	onChange={this.changeFilter.bind(this, 0)}
                >
					<Option value=''></Option>
                    {
                        this.state.collegeLists.map((item, index) => <Option key={index} title={item.name}   value={item.value}>{item.name}</Option>)
                    }
                </Select>

                <Select 
                	value={this.state.curMajor}
                	placeholder="" 
                	style={{ width: 150, marginRight: 10 }} 
                	onChange={this.changeFilter.bind(this, 1)}
                >
					<Option value=''></Option>
                    {
                        this.state.majorLists.map((item, index) => <Option key={index} title={item.name} value={item.value}>{item.name}</Option>)
                    }
                </Select>

                <Select
					value = {this.state.curGender}
					placeholder=""
					style={{ width: 120, marginRight: 10 }}
					onChange={this.changeFilter.bind(this, 2)}
				>
					{
						this.state.genderLists.map((item, index) => <Option key={index} title={item.name} value={item.value}>{item.name}</Option>)
					}
				</Select>
                
                <Input style={{ width: 180, marginRight: 10 }} value={this.state.studentNo} onChange={this.changeStudentNo.bind(this)} placeholder="/"></Input>

                <Button type="primary" icon="search" onClick={this.sureBtn}>  </Button>
            </div>
        )
    }
}
